.asteroids {
  margin-top: 40px;
}

.list_order {
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  row-gap: 32px;
}

.button_submit {
  width: 209px;
  height: 56px;
  border: none;
  border-radius: 4px;
  background-color: #186dd6;
  color: #fff;
  font-size: 20px;
  line-height: 32px;
  margin-top: 32px;
  cursor: pointer;
  transition: 0.5s;
}

.button_submit:hover {
  opacity: 0.8;
}

@media screen and (max-width: 790px) {
  .list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 540px) {
  .list {
    grid-template-columns: repeat(1, 1fr);
  }
}
.asteroid-description {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 8px;
  background-color: white;
  color: black;
}

.asteroid-description__text {
  color: black;
}

